<blockquote>
<p><strong>友链</strong>（blogroll）是一种将互联网上的一个个孤岛似的<em>个人</em>网站联系起来的一种高效方式。这使得各个网站可以彼此串通，使访问者有更多的机会光顾每一个相连接的网站。</p>
</blockquote>
