<h2>如何在本站上添加友链？<a class="header-anchor" id="%E5%A6%82%E4%BD%95%E5%9C%A8%E6%9C%AC%E7%AB%99%E4%B8%8A%E6%B7%BB%E5%8A%A0%E5%8F%8B%E9%93%BE%EF%BC%9F" href="#%E5%A6%82%E4%BD%95%E5%9C%A8%E6%9C%AC%E7%AB%99%E4%B8%8A%E6%B7%BB%E5%8A%A0%E5%8F%8B%E9%93%BE%EF%BC%9F">#</a></h2>
